import React, { Component } from 'react';

import {connect} from 'react-redux';

import classnames from 'classnames';

import {login} from '@data/actions/sign';

import tooltip from '@component/common/tooltip';

import {Link} from 'react-router';
import history from '@history/history'

import {setToken} from '@utils/token.js'
@connect(
    state => ({
        
    }),
    {
      login
    }
  )
export default class Login extends Component {


  login = () => {
      let {login} = this.props;
      let userName = this.refs.name.value.trim()
      let pwd = this.refs.auth.value.trim()
      if(!userName || !pwd) {
        tooltip.show({
          type:'error',
          content: ''
        });
        return;
      }
      let options = {
        apiUrl: WebIM.config.apiURL,
        appKey: WebIM.config.appkey,
        user: userName,
        pwd: pwd
      };

      login(options).then((token)=>{
        //\
        //cookie http-only
        //localStorage
        // btoabase64 atob
        setToken(token)
        history.push('/chat')
      }).catch(()=>{
        //tooltip
        tooltip.show({
          type: 'error',
          content: ''
        })
      })
     
  }
  render() {
      let mainClassName = classnames({
        'sign': true,
        'signup': true
    });
    let {regState} = this.props;
    return (
        <div className={mainClassName}>
            <h2></h2>
            <input ref='name' 
                name="name" 
                className="input"
                placeholder='' 
                autoFocus={true} 
                onInput={this.keyDown}/>
            <input ref='auth' 
                name="auth" 
                className="input"
                placeholder='' 
                type='password' 
                onInput={this.keyDown}/>
            <button className="button" onClick={this.login}></button>
            <p>,
                <Link to="/signup"> </Link>
            </p>
        </div>
    )
  }
}


